<!-- Êî∂ËøêËÆ∞ÂΩï-->
<template>
    <view class="container">
        <PageHeader title="Êî∂ËøêËÆ∞ÂΩï" @back="back" />
        <view class="menu">
            <view class="search-container">
                <view class="search-box">
                    <uni-icons type="search" size="24" color="#999" class="search-icon"></uni-icons>
                    <input class="search-input" placeholder="ÊêúÁ¥¢Â∫óÈì∫/ÂïÜÈì∫ÂÖ≥ÈîÆËØç" v-model="searchKeyword" @confirm="onSearch" />
                    <uni-icons v-if="searchKeyword" type="clear" size="20" color="#999" class="clear-icon"
                        @click="clearSearch"></uni-icons>
                </view>
            </view>
            <view class="tab-bar">
                <view v-for="(tab, index) in tabs" :key="index" class="tab-item"
                    :class="{ active: currentTab === index }" @click="handleTabClick(index)">
                    {{ tab.value }}
                    <!-- ‰∏∫È¢ÑÁ∫¶‰∏≠tabÊ∑ªÂä†uni-badge -->
                    <uni-badge v-if="tab.value === 'ÂæÖÂ§ÑÁêÜ' && bookingBadgeText !== 0" class="uni-badge" type="error"
                        :text="bookingBadgeText" :is-dot="false" absolute="rightTop" :offset="[-5, -12]"></uni-badge>

                    <view class="tab-line" v-if="currentTab === index"></view>
                </view>
            </view>
        </view>

        <!-- ÂÜÖÂÆπÂå∫Âüü -->
        <view class="content-wrapper">
            <scroll-view class="content" scroll-y>
                <!-- Êï∞ÊçÆÂàóË°® -->
                <view class="order-list" v-if="allOrderList.length > 0">
                    <view class="order-item" v-for="(item, index) in allOrderList" :key="index">
                        <view class="order-header">
                            <view class="shop-info">
                                <text class="shop-name">{{ item.merchantName }}</text>
                                <DriverStatusTag :status="item.status" />
                            </view>
                        </view>
                        <view class="order-content">
                            <InfoDisplay :fields="getInfoFields(item)" />
                        </view>
                        <DriverOrderActions :status="item.status" :order-data="item" @refresh="handleRefresh"
                            @abnormalReport="handleAbnormalReport" />
                    </view>

                    <!-- Âä†ËΩΩÊõ¥Â§öÁªÑ‰ª∂ - Âè™Âú®ÊúâÊï∞ÊçÆÊó∂ÊòæÁ§∫ -->
                    <view class="loadMore">
                        <uni-load-more :status="loadingStatus" :content-text="{
                            contentdown: '‰∏äÊãâÊòæÁ§∫Êõ¥Â§ö',
                            contentrefresh: 'Ê≠£Âú®Âä†ËΩΩ...',
                            contentnomore: 'Ê≤°ÊúâÊõ¥Â§öÊï∞ÊçÆ‰∫Ü'
                        }"></uni-load-more>
                    </view>
                </view>

                <!-- ÊöÇÊó†Êï∞ÊçÆÁä∂ÊÄÅ -->
                <view class="empty-state" v-else-if="loadingStatus !== 'loading'">
                    <view class="empty-icon">üìã</view>
                    <text class="empty-text">ÊöÇÊó†Êï∞ÊçÆ</text>
                    <text class="empty-desc">ÂΩìÂâçÂàÜÁ±ª‰∏ãÊöÇÊó∂Ê≤°ÊúâÁõ∏ÂÖ≥‰ø°ÊÅØ</text>
                </view>
            </scroll-view>
        </view>

        <!-- ÂºÇÂ∏∏‰∏äÊä•ÂºπÁ™ó -->
        <AbnormalReportModal :show="showAbnormalModal" :order-data="currentOrderData" @close="closeAbnormalModal"
            @success="handleAbnormalSuccess" />
    </view>
</template>
<script setup>
import {
    ref,
    computed,
    onMounted
} from 'vue';
import {
    onPullDownRefresh,
    onReachBottom
} from '@dcloudio/uni-app';

import {
    apiGetDriverPlanPage, apiGetnoNeedCollect, apiGetdriverConfirmPlan, apiGetDriverNotConfirmNum
} from '@/api/apis.js';

import { onShow } from '@dcloudio/uni-app' // ÂØºÂÖ•onShowÁîüÂëΩÂë®Êúü
import DriverStatusTag from '@/components/DriverStatusTag/DriverStatusTag.vue'
import InfoDisplay from '@/components/InfoDisplay/InfoDisplay.vue'
import DriverOrderActions from '@/components/DriverOrderActions/DriverOrderActions.vue'
import AbnormalReportModal from '@/components/AbnormalReportModal/AbnormalReportModal.vue'
import PageHeader from '@/components/PageHeader/PageHeader.vue'
import { formatWeight, formatNum } from '@/utils/orderUtils'
import { useUserStore } from '@/stores/user.js'


const tabs = [{ key: "3", value: "ÂéÜÂè≤ËÆ∞ÂΩï" }, { key: "0", value: "ÂæÖÂ§ÑÁêÜ" }, { key: "1", value: "Â∑≤ÂÆåÊàê" }];
//‰∏ãÊ†á
const currentTab = ref(1);

// ÂΩìÂâçÈÄâ‰∏≠tabÁöÑstatusÂÄºÔºàËΩ¨Êç¢‰∏∫Êï¥Êï∞Ôºâ
const currentStatusKey = computed(() => parseInt(tabs[currentTab.value].key));

// Á≠õÈÄâÁõ∏ÂÖ≥Áä∂ÊÄÅ
const searchKeyword = ref(''); // ÊêúÁ¥¢ÂÖ≥ÈîÆËØç

// Ê†πÊçÆÁä∂ÊÄÅËé∑Âèñ‰ø°ÊÅØÂ≠óÊÆµ
const getInfoFields = (item) => {
    const status = item.status;

    // Áä∂ÊÄÅ‰∏∫ 0ÔºàËøõË°å‰∏≠ÔºâÊàñ 2ÔºàÊó†Ê≥ïÊî∂ËøêÔºâÊó∂ÊòæÁ§∫È¢Ñ‰º∞‰ø°ÊÅØ
    if (status === 0 || status === '0' || status === 2 || status === '2') {
        return [
            {
                key: 'appointmentTime',
                label: 'È¢Ñ‰º∞Êó∂Èó¥',
                value: item.appointmentTime
            },
            {
                key: 'estimateWeight',
                label: 'È¢Ñ‰º∞ÈáçÈáè',
                value: item.estimateWeight
            },
            {
                key: 'estimateBucketNum',
                label: 'È¢Ñ‰º∞Ê°∂Êï∞',
                value: item.estimateBucketNum
            },
            {
                key: 'address',
                label: 'Âú∞ÂùÄ',
                value: item.address
            }
        ];
    }

    // Áä∂ÊÄÅ‰∏∫ 1ÔºàÂ∑≤ÂÆåÊàêÔºâÊó∂ÊòæÁ§∫Êî∂Ëøê‰ø°ÊÅØ
    if (status === 1 || status === '1') {
        return [
            {
                key: 'arrivalTime',
                label: 'Êî∂ËøêÊó∂Èó¥',
                value: item.arrivalTime
            },
            {
                key: 'weight',
                label: 'Êî∂ËøêÈáçÈáè',
                value: item.weight
            },
            {
                key: 'bucketNum',
                label: 'Êî∂ËøêÊ°∂Êï∞',
                value: item.bucketNum
            },
            {
                key: 'address',
                label: 'Âú∞ÂùÄ',
                value: item.address
            }
        ];
    }

    // ÈªòËÆ§ËøîÂõûÈ¢Ñ‰º∞‰ø°ÊÅØ
    return [
        {
            key: 'appointmentTime',
            label: 'È¢Ñ‰º∞Êó∂Èó¥',
            value: item.appointmentTime
        },
        {
            key: 'estimateWeight',
            label: 'È¢Ñ‰º∞ÈáçÈáè',
            value: item.estimateWeight
        },
        {
            key: 'estimateBucketNum',
            label: 'È¢Ñ‰º∞Ê°∂Êï∞',
            value: item.estimateBucketNum
        },
        {
            key: 'address',
            label: 'Âú∞ÂùÄ',
            value: item.address
        }
    ];
};

const userStore = useUserStore();

//ËøîÂõû‰∏ä‰∏ÄÈ°µ
const back = () => {
    uni.navigateBack()
}

//ÁÇπÂáªËèúÂçï
function handleTabClick(index) {
    currentTab.value = index;
    allOrderList.value = [];
    pageNum.value = 1; // ÈáçÁΩÆÈ°µÁ†Å‰∏∫1
    getNetwork();
}

// È°µÈù¢ÊòæÁ§∫Êó∂Âà∑Êñ∞Êï∞ÊçÆ
onShow(async () => {
    console.log('È°µÈù¢ÊòæÁ§∫Êó∂Âà∑Êñ∞Êï∞ÊçÆ')
    getDriverNotConfirmNum();
    getNetwork();
})

// Â§ÑÁêÜÂºÇÂ∏∏‰∏äÊä•ÂêéÁöÑÂà∑Êñ∞
const handleRefresh = async () => {
    try {
        // ÈáçÁΩÆÈ°µÁ†ÅÂπ∂ÈáçÊñ∞Ëé∑ÂèñÊï∞ÊçÆ
        allOrderList.value = [];
        pageNum.value = 1;
        await getNetwork();
        await getDriverNotConfirmNum();
    } catch (error) {
        console.error('Âà∑Êñ∞Êï∞ÊçÆÂ§±Ë¥•:', error)
    }
}

// ÂºÇÂ∏∏‰∏äÊä•Áõ∏ÂÖ≥ÂèòÈáè
const showAbnormalModal = ref(false)
const currentOrderData = ref(null)

// Â§ÑÁêÜÂºÇÂ∏∏‰∏äÊä•‰∫ã‰ª∂
const handleAbnormalReport = (orderData) => {
    console.log('ÂºÇÂ∏∏‰∏äÊä•‰∫ã‰ª∂', orderData)
    currentOrderData.value = orderData
    showAbnormalModal.value = true
}

// ÂÖ≥Èó≠ÂºÇÂ∏∏‰∏äÊä•ÂºπÁ™ó
const closeAbnormalModal = () => {
    showAbnormalModal.value = false
    currentOrderData.value = null
}

// ÂºÇÂ∏∏‰∏äÊä•ÊàêÂäüÂõûË∞É
const handleAbnormalSuccess = async () => {
    // Âà∑Êñ∞Êï∞ÊçÆ
    await handleRefresh()
}



// ÊåâÈíÆÁÇπÂáª‰∫ã‰ª∂Â§ÑÁêÜÂáΩÊï∞Â∑≤Â∞ÅË£ÖÂà∞ DriverOrderActions ÁªÑ‰ª∂‰∏≠

// Ê∑ªÂä†badgeTextÁöÑrefÂèòÈáè
const bookingBadgeText = ref(0);

const getDriverNotConfirmNum = async () => {
    const res = await apiGetDriverNotConfirmNum({
        driverId: userStore.sfmerchant?.id
    });
    if (res.code === 200) {
        bookingBadgeText.value = res.data ?? 0;
    }
};


// Ê∑ªÂä†È°µÁ†ÅÂíåÂä†ËΩΩÁä∂ÊÄÅÂèòÈáè
const pageNum = ref(1);
const loadingStatus = ref('more'); // more-Âä†ËΩΩÂâç/loading-Âä†ËΩΩ‰∏≠/nomore-Ê≤°ÊúâÊõ¥Â§öÊï∞ÊçÆ

// Êï∞ÊçÆÂàóË°®
const allOrderList = ref([]);

//ÂêéËé∑ÂèñÊï∞ÊçÆ
const getNetwork = async () => {
    try {
        // Â¶ÇÊûú‰∏çÊòØÁ¨¨‰∏ÄÈ°µÔºåËÆæÁΩÆÂä†ËΩΩÁä∂ÊÄÅ‰∏∫Âä†ËΩΩ‰∏≠
        if (pageNum.value > 1) {
            loadingStatus.value = 'loading';
        }

        // ÊûÑÂª∫ËØ∑Ê±ÇÂèÇÊï∞
        const params = {
            pageNum: pageNum.value,
            driverId: userStore.sfmerchant?.id,
            status: currentStatusKey.value // ‰ΩøÁî®ÂΩìÂâçÈÄâ‰∏≠tabÂØπÂ∫îÁöÑÊï¥Êï∞Á±ªÂûã
        };

        // Ê∑ªÂä†ÊêúÁ¥¢ÂÖ≥ÈîÆËØç
        if (searchKeyword.value) {
            params.title = searchKeyword.value;
        }

        const res = await apiGetDriverPlanPage(params);


        // Â§ÑÁêÜ‰∏ãÊãâÂà∑Êñ∞
        if (pageNum.value === 1) {
            allOrderList.value = res.data.list || [];
            uni.stopPullDownRefresh();
        } else {
            // Â§ÑÁêÜ‰∏äÊãâÂä†ËΩΩÊõ¥Â§ö
            allOrderList.value = [...allOrderList.value, ...(res.data.list || [])];
        }

        // Âà§Êñ≠ÊòØÂê¶ËøòÊúâÊõ¥Â§öÊï∞ÊçÆ
        if (res.data.list && res.data.list.length < 10) {
            // Â¶ÇÊûúËøîÂõûÁöÑÊï∞ÊçÆÂ∞ë‰∫éÊØèÈ°µÊï∞ÈáèÔºåËØ¥ÊòéÊ≤°ÊúâÊõ¥Â§öÊï∞ÊçÆ‰∫Ü
            loadingStatus.value = 'nomore';
        } else {
            // Âê¶ÂàôËøòÊúâÊõ¥Â§öÊï∞ÊçÆ
            loadingStatus.value = 'more';
        }
    } catch (error) {
        console.error('Ëé∑ÂèñÊï∞ÊçÆÂ§±Ë¥•:', error);

        // ÂÅúÊ≠¢‰∏ãÊãâÂà∑Êñ∞
        uni.stopPullDownRefresh();

        // ÈáçÁΩÆÂä†ËΩΩÁä∂ÊÄÅ
        loadingStatus.value = 'more';

        // Â¶ÇÊûúÊòØÁ¨¨‰∏ÄÈ°µÂä†ËΩΩÂ§±Ë¥•ÔºåÁ°Æ‰øùÊòæÁ§∫ÊöÇÊó†Êï∞ÊçÆÁä∂ÊÄÅ
        if (pageNum.value === 1) {
            allOrderList.value = [];
        }

        uni.showToast({
            title: 'Êï∞ÊçÆÂä†ËΩΩÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
            icon: 'none',
            duration: 2000
        });
    }
};

// ÊêúÁ¥¢ÊñπÊ≥ï
const onSearch = () => {

    resetPageAndReload();
};



// Ê∏ÖÁ©∫ÊêúÁ¥¢ÂÖ≥ÈîÆËØç
const clearSearch = () => {
    searchKeyword.value = '';
    resetPageAndReload();
};

const resetPageAndReload = () => {
    console.log('ÈáçÁΩÆÈ°µÁ†ÅÂíåÈáçÊñ∞Âä†ËΩΩÊï∞ÊçÆ');
    allOrderList.value = [];
    pageNum.value = 1;
    getNetwork();

};


// ‰∏äÊãâÂä†ËΩΩÊõ¥Â§öÊñπÊ≥ï
const onLoadMore = () => {
    if (loadingStatus.value === 'nomore') return;
    // È°µÁ†ÅÂ¢ûÂä†
    pageNum.value++;
    getNetwork();
};


// Ëß¶Â∫ïÂä†ËΩΩÊõ¥Â§ö
onReachBottom(() => {
    onLoadMore();
});

//‰∏ãÊãâÂà∑Êñ∞
onPullDownRefresh(() => {
    allOrderList.value = [];
    pageNum.value = 1; // ÈáçÁΩÆÈ°µÁ†Å‰∏∫1
    getNetwork();
    getDriverNotConfirmNum()
})

// ÁªÑ‰ª∂ÊåÇËΩΩÊó∂ÂàùÂßãÂåñÊï∞ÊçÆ
onMounted(() => {
    pageNum.value = 1;
    getNetwork();
    getDriverNotConfirmNum()
});
</script>

